* {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    color: rgb(33, 33, 33)
}

html {
    min-height: 200vh;
    max-height: 300vh;
}

html, body {
    background-color: rgb(33, 33, 33);
}

section, p {
    font-family: 'Archivo', sans-serif;;
}

h1 {
    font-size: 5rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    color: rgb(217, 202, 179);
    padding-bottom: 0.2rem;
    
}

section {
    padding-top: 0.5rem;
    text-align: justify;
}

img {
    width: 22px;
    height: 22px;
}

/* ALL COMMON STYLES FOR COMPONENTS GOES HERE */

.edit, .add, .del {
    cursor: pointer;
}


.sectionHeader {
    display: flex;
    justify-content: space-between;
    border-bottom: 1.5px solid rgb(172, 172, 172);
}

.btnContainer {
    position: relative;
}

.submitBtn {
    width: 4rem;
    height: 1.5rem;
    background-color: rgb(60, 102, 84);
    border: none;
    border-radius: 5px;
    color: white;
    justify-self: end;
    align-self: end;
}

.cvForm {
    background-color: rgba(226, 240, 238, 0.98);
    border-radius: 2px;
    box-shadow: 0 0 3px black;
    position: absolute;
    right: 0%;
    width: 15rem;
    height: 16rem;
    padding: 1rem;
    display: grid;
    z-index: 2;
}

.inputContainer {
    display: grid;
}